.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgb(175, 216, 248);
  display: inline-block;
  margin: 0px 0.5px -3px;
}

.node-finish {
  background-color: rgb(241, 131, 131);
  background-image: url(circle.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.node-start {
  background-color: rgb(238, 240, 126);
  background-image: url(triangel-right.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(0, 218, 207);
  }
}

.node-wall {
  background-color: rgb(12, 53, 71);
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
    background-image: url(path.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(106, 255, 118);
    background-image: url(path.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 141, 106);
    background-image: url(path.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}
